<template>
  <div id="setInviteCode" :class="{hasHeader: $utils.showNavBar()}">
    <headerNav :msg='msg' v-if="$utils.showNavBar()"></headerNav>
    <div v-if="inviteUser && isShow" class="inviteBox">
      <div class="inviteTitle">您的邀请人是</div>
      <div class="iconSize avatar">
        <img :src="inviteUser.avatar" alt="头像">
      </div>
      <div class="nickname">{{inviteUser.nickname}}</div>
      <div class="inviteTime">邀请时间：{{$utils.timestampToStr(inviteUser.invitee_time * 1000, 'Y-M-D')}}</div>
    </div>
    <div v-if="!inviteUser && isShow">
      <div class="loginOn flex-column jusB" v-if="commonState.userInfo.is_new === 1 || !commonState.login_bol">
        <div class="topTip">填写好友邀请码立得<span class="redColor">{{commonState.inviteVcoin * 1}}掘金宝（原始股）</span>奖励</div>
        <div class="input">
          <input type="number" :placeholder="placeholderText" @keyup="setNumber()" pattern="[0-9]" @focus="setHolder(1)" @blur="setHolder(0)" v-model="inviteCode">
        </div>
        <div class="bottomButton">
          <div class="button" @click="confirm">提交领取奖励</div>
        </div>
      </div>
      <div class="loginOn oldUser" v-else>
        <div class="outTime bgIcon"></div>
        <div class="outTip">您注册已超过30天，无法输入邀请码</div>
      </div>
    </div>
    <div class="rect"></div>
    <div class="bottomTip">
      <div class="topBg bgIcon"></div>
      <div class="redColor flex-row jusC">邀请好友</div>
      <div class="flex-row tipText">邀请好友最高获得<span class="redColor">{{commonState.inviteVcoin * 10}}掘金宝（原始股）</span>的奖励</div>
      <!-- <div class="flex-row tipText">您的好友邀请成功，您还可以<span class="redColor">额外获得现金奖励</span></div> -->
      <div class="flex-row tipText">您的好友还可以为您<span class="redColor">源源不断赚金币换原始股</span></div>
      <div class="buttonBox">
        <div class="bottomButton" @click="toUrl('/invite_friend')">邀请好友去</div>
      </div>
    </div>
    <bindPhone v-if="showBindPhone" :phoneParams='{title: "绑定手机号", confirm: "立即绑定"}' :errChange='codeUpdate' @closeBindPhone="closeBindPhone"></bindPhone>
    <ConnectWechat v-if="showWechat" @closeWechat='closeWechat'></ConnectWechat>
    <confirmBox v-if="showConfirmBox" :config='configText' @closeConfirmBox='closeConfirmBox'></confirmBox>
    <ChooseAccount v-if="showChoose" :config='configText' @closeChoose='closeChoose'></ChooseAccount>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
import headerNav from '@/components/task/header-nav'
import bindPhone from '@/components/common/bind_phone'
import ConnectWechat from '@/components/task/connect_wechat'
import confirmBox from '@/components/task/confirmBox'
import ChooseAccount from '@/components/task/chooseAccount'

export default {
  components: {
    headerNav,
    bindPhone,
    confirmBox, // 确认弹框
    ConnectWechat,
    ChooseAccount
  },
  computed: mapState({
    commonState: state => state.common,
    share: state => state.share
  }),
  data () {
    return {
      inviteCode: '',
      showChoose: false,
      auths: {}, // 授权对象
      showBindPhone: false,
      inviteReward: 10,
      showConfirmBox: false,
      isShow: false,
      configText: {},
      showWechat: false,
      msg: null,
      placeholderText: '输入邀请码（向介绍人索取）',
      inviteUser: null,
      codeUpdate: 0
    }
  },
  methods: {
    ...mapMutations([
      'common_login_box',
      'setShowShare',
      'set_shareSuccessObj',
      'set_shareType',
      'common_set_vcoin'
    ]),
    // 设置提示文本
    setHolder (flag) {
      if (flag) {
        this.placeholderText = ''
      } else {
        this.placeholderText = '输入邀请码（向介绍人索取）'
      }
    },
    setNumber () {
      if (this.inviteCode) {
        this.inviteCode = this.inviteCode.replace(/\D/g, '')
        this.inviteCode = this.inviteCode.substr(0, 9)
      } else {
        this.inviteCode = null
      }
    },
    // 绑定微信成功
    bindWechatScucess (res) {
      if (res.data.coin > 0) {
        let shareSuccessObj = Object.assign({type: 3, tipText: `已成功将微信${this.$utils.splitStr(res.data.wechat_nickname)}关联到当前账户上`}, res.data)
        this.set_shareSuccessObj({
          type: 16,
          shareSuccessObj
        })
        this.set_shareType(16)
        this.setShowShare(true)
        this.showChoose = false
      } else {
        this.$toast({
          message: `已成功将微信${this.$utils.splitStr(res.data.wechat_nickname)}关联到当前账户上`,
          duration: 1500
        })
      }
      this.getInviteUser()
      this.showWechat = false
      this.showConfirmBox = false
    },
    // e无数据关闭，有数据请求关联成功奖励接口后关闭
    closeWechat (e) {
      if (e) {
        if (this.$config.source_style >= 6) {
          // app认证
          let w = window.plus.nativeUI.showWaiting('认证中')
          this.auths['weixin'].authorize(res => {
            let data = {
              code: res.code
            }
            this.$api.post('APP_BIND', data, res => {
              this.bindWechatScucess(res)
            }, res => {
              w.close()
              if (res.code === 801) {
                this.configText = res.data
                this.showConfirmBox = true
                this.showWechat = false
              }
            })
            w.close()
          }, () => {
            w.close()
            this.$toast({
              message: '认证失败',
              duration: 1500
            })
          })
          // 微信认证
        } else {
          let data = {
            unionid: this.$utils.cache('unionid')
          }
          this.$api.post('WECHAT_BIND', data, res => {
            this.bindWechatScucess(res)
          }, res => {
            if (res.code === 801) {
              this.configText = res.data
              this.showWechat = false
              this.showConfirmBox = true
            }
          })
        }
      } else {
        this.showWechat = false
      }
    },
    // 关闭绑定手机号弹窗，有数据时调取绑定手机号接口
    closeBindPhone (e) {
      if (e) {
        let data = {
          account: e.mobile,
          code: e.code
        }
        if (e.captcha_code) {
          data.captcha_code = e.captcha_code
        }
        this.$api.post('BIND_PHONE', data, res => {
          if (!res.data.coin) {
            let shareSuccessObj = Object.assign({type: 5}, res.data)
            this.set_shareSuccessObj({
              type: 16,
              shareSuccessObj
            })
            this.set_shareType(16)
            this.setShowShare(true)
          }
          this.showBindPhone = false
        }, res => {
          if (res.code === 10) {
            this.codeUpdate++
          }
        })
      } else {
        this.showBindPhone = false
      }
    },
    closeChoose ({action, type}) {
      if (action === 'confirm') {
        if (this.$config.source_style >= 6) {
          // app认证
          let w = window.plus.nativeUI.showWaiting('认证中')
          this.auths['weixin'].authorize(res => {
            let data = {
              code: res.code,
              confirm: 1,
              retain_wechat: type
            }
            this.$api.post('APP_BIND', data, res => {
              this.bindWechatScucess(res)
            }, res => {
              if (res.code === 801) {
                this.configText = res.data
                this.showConfirmBox = true
                this.showWechat = false
              }
            })
            w.close()
          }, (res) => {
            w.close()
            this.$toast({
              message: '认证失败',
              duration: 1500
            })
          })
        } else {
          // 微信认证
          let data = {
            unionid: this.$utils.cache('unionid'),
            confirm: 1,
            retain_wechat: type
          }
          this.$api.post('WECHAT_BIND', data, res => {
            this.bindWechatScucess(res)
          }, res => {
            if (res.code === 801) {
              this.configText = res.data
              this.showWechat = false
              this.showConfirmBox = true
            }
          })
        }
      } else {
        this.showChoose = false
      }
    },
    // 确认返回按钮
    closeConfirmBox ({action}) {
      if (action === 'confirm') {
        this.showConfirmBox = false
        this.showWechat = false
        this.showChoose = true
      } else if (action === 'cancel') {
        this.showConfirmBox = false
      } else {
        this.showConfirmBox = false
      }
    },
    // 获取消息提示
    getHint () {
      if (!this.commonState.inviteVcoin) {
        this.$api.get('MESSAGE_HINT', {}, res => {
          this.common_set_vcoin(res.data.reward_coefficient)
        })
      }
    },
    // 获取微信授权登录对象
    getWXLogin () {
      this.auths = {}
      this.$utils.plusReady((plus) => {
        plus.oauth.getServices(services => {
          for (let i in services) {
            let tempService = services[i]
            this.auths[tempService.id] = tempService
          }
        }, services => {
          this.$toast({
            message: '获取登录认证通道失败',
            duration: 1500
          })
        })
      })
    },
    // 返回上一级
    back () {
      this.$router.go(-1)
    },
    // 跳转至邀请好友页面
    toUrl (url) {
      this.$router.push(url)
    },
    // 获取上级邀请人
    getInviteUser () {
      this.$api.get('MYINVITEUSER', {}, res => {
        if (res.data && !res.data.invitee_time) {
          res.data.invitee_time = (new Date().getTime() - 84256 * 1000 * 30) / 1000
        }
        this.inviteUser = res.data
        this.isShow = true
      })
    },
    // 提交邀请码
    confirm () {
      if (!this.commonState.login_bol) {
        return this.common_login_box(true)
      }
      if (!this.inviteCode) {
        return this.$toast({
          message: '请输入邀请码',
          dutation: 1500
        })
      } else if (!this.$utils.isReg('INVITECODE', this.inviteCode)) {
        return this.$toast({
          message: '请输入正确的邀请码',
          dutation: 1500
        })
      } else {
        let data = {
          invitation_code: this.inviteCode
        }
        this.$api.get('SET_INVITECODE', data, res => {
          res.data.vcoin = res.data.coin
          res.data.coin = 0
          let shareSuccessObj = Object.assign({type: 4}, res.data)
          this.set_shareSuccessObj({
            type: 7,
            shareSuccessObj
          })
          this.set_shareType(7)
          this.setShowShare(true)
          this.getInviteUser()
        }, res => {
          if (res.code === 2) {
            this.$toast({
              message: res.msg
            })
            // 显示绑定手机弹框
          } else if (res.code === 803) {
            this.showBindPhone = true
            this.$toast({
              message: '请先绑定手机号'
            })
          } else if (res.code === 802) {
            this.showWechat = true
            this.$toast({
              message: '请绑定微信'
            })
          }
        })
      }
    }
  },
  beforeUpdate () {
    this.setNumber()
  },
  watch: {
    'commonState.login_box' (flag) {
      if (!flag && this.commonState.login_bol) {
        this.getInviteUser()
      }
    },
    'share.showSuccessShare' (flag) {
      if (!flag && this.share.shareType === 16) {
        this.getInviteUser()
      }
    }
  },
  mounted () {
    if (!this.commonState.login_bol && this.commonState.userInfo.isNew === 1) {
      this.$toast({
        message: '请先登录后输入'
      })
    }
    this.getHint()
    this.getInviteUser()
    this.getWXLogin()
    // 默认不显示输入框，以免输入和邀请人标识一下闪过
    setTimeout(() => {
      this.isShow = true
    }, 1000)
  }
}
</script>
<style lang="scss" scoped>
  #setInviteCode{
    font-size: 30px;
    height: 100%;
    box-sizing: border-box;
    overflow: auto;
    padding-top: 1px;
    &.hasHeader {
      padding-top: 88px;
    }
    .inviteBox {
      text-align: center;
      overflow: hidden;
      .inviteTitle {
        margin: 30px auto;
        font-size: 32px;
        line-height: 45px;
      }
      .avatar {
        margin: 0 auto;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        overflow: hidden;
      }
      .nickname {
        font-size: 26px;
        margin: 20px auto 50px;
        line-height: 38px;
      }
      .inviteTime {
        margin-bottom: 33px;
        color: #999
      }
    }
    .rect {
      height: 20px;
      background: #f5f5f5;
      color: #232323;
    }
    .loginOn{
      text-align: center;
      height: 400px;
      box-sizing: border-box;
      padding: 50px 0;
      .topTip{
        line-height: 70px;
      }
      .input{
        height: 90px;
        margin: 20px 0 26px;
        width: 100%;
        padding: 0 30px;
        box-sizing: border-box;
        display: flex;
        input{
          height: 90px;
          flex: 1;
          font-size: 30px;
          border-radius: 45px;
          border: 1px solid #ddd;
          padding: 0 20px;
          text-align: center;
          box-sizing: border-box;
        }
      }
      .bottomButton{
        width: 100%;
        padding: 0 30px;
        box-sizing: border-box;
        .button{
          width: 690px;
          background: #F5A623;
          height: 90px;
          border-radius: 45px;
          color: #fff;
          line-height: 90px;
        }
      }
      .outTime{
        background-image: url('../../../assets/img/task/out_time.png');
        height: 154px;
        background-size: 195px 154px;
      }
    }
    .oldUser{
      height: 383px;
      overflow: hidden;
      box-sizing: border-box;
      .outTime {
        margin: 40px 0 48px;
      }
      .outTip {
        padding-bottom: 48px;
        line-height: 42px;
      }
    }
    .bottomTip{
      padding: 100px 0 0;
      .topBg{
        height: 142px;
        background-image: url('../../../assets/img/task/time_coin.png');
        background-size: 221px 142px;
      }
      >.redColor{
        margin: 30px 0;
      }
      .tipText{
        padding: 0 20px;
        justify-content: flex-start;
        margin-bottom: 20px;
        &::before{
          content: '';
          display: inline-block;
          width: 14px;
          height: 14px;
          border-radius: 50%;
          overflow: hidden;
          background: #FF5951;
          margin-right: 10px;
        }
      }
      .buttonBox{
        padding: 0 30px;
        margin-top: 162px;
        .bottomButton{
          background: #F93E21;
          height: 90px;
          border-radius: 45px;
          text-align: center;
          line-height: 90px;
          color: #fff;
        }
      }
    }

  }
</style>



